import React from 'react'
import { styled } from 'styled-components'

const TypographyBox = styled.div`
display: inline-block;
font-weight: ${(props) => {
    if(props.type ==="bold"){
        return "bold"
    } else if(props.type === "light"){
        return "light"
    } else {
        return "normal"
    }
    }};
`

const Typography = ({ children, type, ...rest }) => {
  return (
    <TypographyBox type={type}{...rest}>{children}</TypographyBox>
  )
}

export default Typography