export const baseUrl = "https://apis.data.go.kr/B551011/KorService1";
export const pageNo = 1;
export const numOfRows = 20000

const secondArray = Object.values(cate);

export const randomNumber = (arr) => {
  return Math.floor(Math.random() * (arr.length));
};

export const sortedAnsList = (totalAns) =>{
  const newArray = [];
  for (const firstItem of totalAns) {
    for (const secondItem of secondArray) {
      if (firstItem.QNum === secondItem.QNum) {
        const randomIdx= randomNumber(secondItem.AnsList[firstItem.AnsIdx])

        newArray.push({
          QNum: firstItem.QNum,
          Ans: secondItem.AnsList[firstItem.AnsIdx][randomIdx],
        });
        break;
      }
    }
  }
  return newArray

}

export const getURL = ({contentTypeId,cat1,cat2,cat3}) => {
  let url;

  // console.log(`https://apis.data.go.kr/B551011/KorService1/areaBasedList1?serviceKey=vm%2Bfp4QVWvHi67%2BP5TMrf4JzHwjRtRa97F44EdllIcEr4CF2Dr6Rl8pLYD%2F7lfWWnIWz%2BaQbfVajXBt0jetlng%3D%3D&&pageNo=1&numOfRows=20000&MobileApp=AppTest&MobileOS=ETC&arrange=A&contentTypeId=12&_type=json`);

if (cat1.length === 3 && cat2.length === 5 && cat3.length === 9) {
  url = `${baseUrl}/areaBasedList1?serviceKey=${serviceKey}&pageNo=${pageNo}&numOfRows=${numOfRows}&MobileApp=AppTest&MobileOS=ETC&arrange=A&contentTypeId=${contentTypeId}&cat1=${cat1}&cat2=${cat2}&cat3=${cat3}&_type=json`;
} else if (cat1.length === 3 && cat2.length === 5 && cat3.length !== 9) {
  url = `${baseUrl}/areaBasedList1?serviceKey=${serviceKey}&pageNo=${pageNo}&numOfRows=${numOfRows}&MobileApp=AppTest&MobileOS=ETC&arrange=A&contentTypeId=${contentTypeId}&cat1=${cat1}&cat2=${cat2}&_type=json`;
} else if (cat1.length === 3 && cat2.length !== 5 && cat3.length !== 9) {
  url = `${baseUrl}/areaBasedList1?serviceKey=${serviceKey}&pageNo=${pageNo}&numOfRows=${numOfRows}&MobileApp=AppTest&MobileOS=ETC&arrange=A&contentTypeId=${contentTypeId}&cat1=${cat1}&_type=json`;
} else if (cat1.length !== 3 && cat2.length !== 5 && cat3.length !== 9) {
  url = `${baseUrl}/areaBasedList1?serviceKey=${serviceKey}&pageNo=${pageNo}&numOfRows=${numOfRows}&MobileApp=AppTest&MobileOS=ETC&arrange=A&contentTypeId=${contentTypeId}&_type=json`;
}

return url
};

export const getURLs = (resultList) => {

    let urlsArr =[];

    for (let index = 0; index < resultList.length; index++) {
      getURL(resultList[index].Ans)
  
      urlsArr.push(getURL(resultList[index].Ans))
    }
  
    return urlsArr

}

export const makeRandom = (itemArr) => {
  const randomIdx = randomNumber(itemArr)

  const tempSpot = itemArr[randomIdx];

  if (tempSpot.firstimage.length === 0 ||tempSpot.firstimage==="") {
    return makeRandom(itemArr);
  } else {
    return tempSpot
  }
};

const replaceArr = replace.item

export const getRandomSpot = async (url) => {
  try {
    const res = await axios.get(url)

    if(res.data?.response === undefined ||res.data?.response === null){
      return makeRandom(replaceArr)
    }
    const itemList = res.data.response.body.items.item;
    const result = makeRandom(itemList);
    // console.log(result);
    return result;
  } catch (err) {
    console.log("에러", err);
    return []
    // throw err; 
  }
};


